import { Component, OnInit, ViewChild } from '@angular/core';
import { TagService } from '../shared/tag.service';
import { ToastController, IonContent } from '@ionic/angular';
import { Tag } from '../shared/tag.model';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-tags',
  templateUrl: './tags.page.html',
  styleUrls: ['./tags.page.scss'],
})
export class TagsPage implements OnInit {
  @ViewChild(IonContent) content: IonContent;
  constructor(public tagService : TagService,
    private toastController : ToastController) { }


  async presentToast(text, theme) {
    const toast = await this.toastController.create({
      message: text,
      showCloseButton: true,
      position: 'top',
      closeButtonText: 'Done',
      color: theme
    });
    toast.present();
  }

  ngOnInit() {          
    this.tagService.getAllTags(); 
    this.resetForm();
  }

  showForEdit(tg: Tag) {
    this.tagService.selectedTag = Object.assign({}, tg);
    this.ScrollToTop();
  }

  ScrollToTop() {
    this.content.scrollToTop(800);
  }

  onDelete(id: number) {
    if (confirm('Are you sure to delete this tag ?') == true) {
      this.tagService.deleteTag(id)
        .subscribe(x => {
          this.tagService.getAllTags();
          this.presentToast('Tag deleted!', 'danger');
        })
    }
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.reset();
    }
    this.tagService.selectedTag = {
      id: null,
      name: ''         
    }
    
  }

  onSubmit(form: NgForm) {
    if (form.value.ID == null) {
      this.tagService.postSprint(form.value).subscribe(data => {
        this.resetForm(form);
        this.tagService.getAllTags();
        this.presentToast('Tag created!', 'success');
      })
    }
    else {
      this.tagService.putTag(form.value.ID, form.value).subscribe(data => {
        this.resetForm(form);
        this.tagService.getAllTags();
        this.presentToast('Tag updated!', 'success');
      })
    }
  }
}
